import styled from "styled-components";

export const ButtonContainer = styled.button`
  text-transform: capitalize;
  font-size: 1.4rem;
  background: transparent;
  border: 0.05rem solid var(--lightBlue);
  border-color: ${props =>
    props.cart
      ? "var(--mainYellow)"
      : props.add
      ? "var(--mainYellow)"
      : props.po
      ? "orange"
      : props.da
      ? "var(--mainYellow)"
      : props.sd
      ? "var(--mainWhite)"
      : "var(--lightBlue)"};

  color: ${props =>
    props.cart
      ? "var(--mainYellow)"
      : props.add
      ? "var(--mainYellow)"
      : props.po
      ? "orange"
      : props.da
      ? "var(--mainYellow)"
      : "var(--lightBlue)"};

  border-radius: 0.5rem;
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  margin: 0.2rem 0.5rem;
  transition: all 0.5s ease-in-out;
  &:hover {
    background: ${props =>
      props.cart
        ? "var(--mainYellow)"
        : props.add
        ? "var(--mainYellow)"
        : props.po
        ? "orange"
        : props.da
        ? "var(--mainYellow)"
        : props.sd
        ? "none"
        : "var(--lightBlue)"};

    color: var(--mainBlue);
  }

  &:focus {
    outline: none;
  }

  .navCart {
    color: var(--mainWhite);
  }

  .navCart :hover {
    color: var(--mainBlue);
  }
`;
